  height: 20px;
  text-align: center;
  }

.appStyles_form__2BSm1:hover{
  box-shadow: 0 2px 36px rgba(0,0,.1,.25); 
}

/*Input focus move up label*/
.appStyles_form_input__1_OIJ:focus + .appStyles_form_label__38DDH{
  top: -.5rem;
  left: .8rem;
  color: #1A73E8;
  color: var(--first-color);
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}


/*Input focus sticky top label*/
.appStyles_form_input__1_OIJ:not(:-ms-input-placeholder).appStyles_form_input__1_OIJ:not(:focus)+ .appStyles_form_label__38DDH{
  top: -.5rem;
  left: .8rem;
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}
.appStyles_form_input__1_OIJ:not(:placeholder-shown).appStyles_form_input__1_OIJ:not(:focus)+ .appStyles_form_label__38DDH{
  top: -.5rem;
  left: .8rem;
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}

/*Input focus*/
.appStyles_form_input__1_OIJ:focus{
  border: 1.5px solid #1A73E8;
  border: 1.5px solid var(--first-color);
}

